import Layout from './components/layout';
import Member from './components/member';
import team from '../static/scripts/team_members';
import styles from './scss/app.scss';

export default () => (
  <Layout>
    <div className={styles.team}>
      {team.map(member => (
        <Member
          key={member.name}
          image={member.image}
          color={member.color}
          name={member.name}
          role={member.role}
          email={member.email}
          about={member.about}
        />
      ))}
    </div>
    <div className={styles.team_footer}>
      <h1>Meet Our Supporters</h1>
      <div className={styles.line} />
      <div className={styles.supporters}>
        <ul>
          <li>Center for Whale Research</li>
          <li>City of Bothell</li>
          <li>Defenders of Wildlife NW</li>
          <li>EarthCorps</li>
          <li>Edson Family</li>
          <li>Evergreen Karate and Jiu Jitsu</li>
          <li>Figure Four Online</li>
          <li>F.O.L.K.S. (Friends of Lime Kiln Society)</li>
          <li>Forterra</li>
          <li />
        </ul>
        <ul>
          <li>Row 2</li>
          <li />
          <li />
          <li />
          <li />
          <li />
          <li />
          <li />
          <li />
        </ul>
        <ul>
          <li>Row 3</li>
          <li />
          <li />
          <li />
          <li />
          <li />
          <li />
          <li />
          <li />
        </ul>
      </div>
    </div>
  </Layout>
);
